.trend{
   width: 40%;
   height: 4vh;
   background-color: #FBBA14;
   color: #161416;
   position: absolute;
   left: 30%;
   top: 0vh;
   border-radius: 0 0 5px 5px;
   -webkit-border-radius: 0 0 5px 5px;
   -moz-border-radius: 0 0 5px 5px;
   -ms-border-radius: 0 0 5px 5px;
   -o-border-radius: 0 0 5px 5px;
}

.spanPosz{
   padding: 10px 0;
   width: 100%;
   position: absolute;
   top: 3vh;
   left: 0;

}
.movwePadding{
   width: 16.4vw;
   margin: 0 3vw;
}
  .styleMoviee{
    width: 13.4vw;
    margin: 1vh 6vw 1vh 0 ;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    position: relative;
cursor: pointer;
height: 25vmax;
}
.styleMoviee img{
  height: 20vmax;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: 0.4s;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
} 


.styleMoviee:hover .posit{
  opacity: 70%;
}
.styleMoviee:hover .pOnee{
   opacity: 100%;
}
.styleMoviee:hover .pOneee{
   opacity: 100%;
}
.styleMoviee:hover .pOne{
   opacity: 100%;
}


.styleMoviee:hover img{
   filter: blur(4px);
   -webkit-filter: blur(4px);
   transform:scale(1.5) ;
   -webkit-transform:scale(1.5) ;
   -moz-transform:scale(1.5) ;
   -ms-transform:scale(1.5) ;
   -o-transform:scale(1.5) ;
}

.styleMoviee:hover .genreId{
   display: block !important;
}




























