@font-face {
    font-family:"Akronim Regular.ttf";
    src: url(../DataImage/Akronim-Regular.ttf);
}

@font-face {
    font-family:"Oswald-VariableFont wght.ttf";
    src: url(../DataImage/Oswald-VariableFont_wght.ttf);
}
@font-face {
    font-family:"Montserrat-Regular.ttf";
    src: url(../DataImage/Montserrat-Regular.ttf);
}

* {
    font-family:"Oswald-VariableFont wght.ttf";
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.around {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.between {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.only-column {
    display: flex;
    flex-direction: column;
}

.only-row {
    display: flex;
    flex-direction: row;
}
.eachPos{
    cursor: pointer;
    height: 8vh;
    font-size: 16px;
    width: 10vw !important;
     margin-right: 1vh;
}
.eachPoss{
    width: 10vw !important;
    font-size: 16px;
    height: 8vh;
    cursor: pointer;
}
.cartEach{
    width: 20px;
    height: 20px;
    margin-left: 1vw;
}
.twoMovie{
    color : white;
    padding: 0 8vw 0 2vw;
    font-family:"Montserrat-Regular.ttf" ;
position: relative;
}
.pHard{
    width: 70%;
}
.oneMovie img{
    width: 22vw;
    height: 70vh;
    border-radius: 15px 15px 0 0;
    -webkit-border-radius: 15px 15px 0 0;
    -moz-border-radius: 15px 15px 0 0;
    -ms-border-radius: 15px 15px 0 0;
    -o-border-radius: 15px 15px 0 0;
}
.oneMovie{
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    color: #0B0D13;
    background-color: white;
    -webkit-box-shadow: 0px 0px 1px 1px rgba(255,255,255,0.98);
    box-shadow: 0px 0px 1px 1px rgba(255,255,255,0.98);
}
.mTitle{
    font-family:"Pacifico-Regular.ttf" !important;
   text-align: center;
   width: 22vw;
   padding-bottom: 4px;
}
.moviE{
    padding: 5vw 0 5vw 5vw;
}
.titleM{
    font-size: 40px;
    font-family:"RacingSansOne-Regular.ttf" !important;
margin-bottom: 2vh;
}
.writeMine{
    color: #D6D7D7 !important;
width: 8vw;
justify-content: flex-start !important;
    font-family:"Oswald-VariableFont wght.ttf" !important;
}

.spanWrite{
    color: #FBBA14;
    font-family:"Oswald-VariableFont wght.ttf" !important;
word-spacing: 5px;
} 
.enoughtMan{
font-size: 16px !important;
width: 8vw !important;
}

.spanOK{
    padding: 0 1vw 0 0;
    align-items: flex-start;
    font-size: 14px !important;
    word-spacing: 1px;
}

.spanShoping{
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0 8.6vw 0 1.9vw;
}














