@font-face {
    font-family:"Gluten-VariableFont_wght.ttf";
    src: url(../DataImage/Gluten-VariableFont_wght.ttf);
}
@font-face {
    font-family:"BigShouldersDisplay-VariableFont_wght.ttf";
    src: url(../DataImage/BigShouldersDisplay-VariableFont_wght.ttf);
}
@font-face {
    font-family:"Montserrat-Regular.ttf";
    src: url(../DataImage/Montserrat-Regular.ttf);
}
@font-face {
    font-family:"Oswald-VariableFont_wght.ttf";
    src: url(../DataImage/Oswald-VariableFont_wght.ttf);
}

@font-face {
    font-family:"RacingSansOne-Regular.ttf";
    src: url(../DataImage/RacingSansOne-Regular.ttf);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
::selection{
    background-color: rgb(90, 90, 90);
    color: black;
}
.column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.around {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.between {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.onlycolumn {
    display: flex;
    flex-direction: column;
}
.onlyrow {
    display: flex;
    flex-direction: row;
}
.trepad{
    margin: 10% 0 0 1.5%;
}
.styleMovie{
    width: 16%;
    margin: 0% 0% 6% 3.3%;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    position: relative;
cursor: pointer;
}
.styleMovie:hover .posit{
   opacity: 70%;
}
.styleMovie:hover .pOnee{
    opacity: 100%;
}
.styleMovie:hover .pOneee{
    opacity: 100%;
}
.styleMovie:hover .pOne{
    opacity: 100%;
}


.styleMovie:hover img{
    filter: blur(4px);
    -webkit-filter: blur(4px);
    transform:scale(1.5) ;
    -webkit-transform:scale(1.5) ;
    -moz-transform:scale(1.5) ;
    -ms-transform:scale(1.5) ;
    -o-transform:scale(1.5) ;
}

.styleMovie:hover .genreId{
    display: block !important;
}

.oneLeaf{
    background-color: #0B0D13;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    z-index: 1;
}

.oneleafImport{
padding: 10vh 0 19.5vh 0}

.posit{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0%;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: 0.4s;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    color: white;
}

.styleMovie img{
    width: 100%;
    height: 23vmax;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: 0.4s;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
} 
.imgDiv{
    width: 100%;
    height: 23vmax !important;
    border-radius: 10px;
    overflow: hidden; 
    -webkit-box-shadow: 0px 0px 1px 1px rgba(255,255,255,0.98);
    box-shadow: 0px 0px 1px 1px rgba(255,255,255,0.98);
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    position: relative; 

}
.pOne{
