@font-face {
    font-family:"Akronim Regular.ttf";
    src: url(../DataImage/Akronim-Regular.ttf);
}

@font-face {
    font-family:"Oswald-VariableFont wght.ttf";
    src: url(../DataImage/Oswald-VariableFont_wght.ttf);
}
@font-face {
    font-family:"Montserrat-Regular.ttf";
    src: url(../DataImage/Montserrat-Regular.ttf);
}

* {
    font-family:"Oswald-VariableFont wght.ttf";
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.around {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.between {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.only-column {
    display: flex;
    flex-direction: column;
}

.only-row {
    display: flex;
    flex-direction: row;
}

.footer{
    background-color: #000000;
    width: 100%;
    height: 15vh;
    padding: 0 2vw;
}
.wordMovie{
    color: whitesmoke;
}
.footUl{
    width: 50%;
}
.footNaw{
color: #898A8C;
font-family:"Montserrat-Regular.ttf" !important;
font-size: 14px;
}
