    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}

.contactOne input{
    background-color: #12151E;
    border: none;
    outline: none;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
