    -ms-border-radius: 25px;
    -o-border-radius: 25px;
    height: 6vh;
    width: 100%;
    padding-left: 2vw;
    font-family:"Montserrat-Regular.ttf"    !important;
}

.aboutTextarea{
    font-family:"Montserrat-Regular.ttf"    !important;
    background-color: #12151E;
    border: none;
    outline: none;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px;
    height: 16vh !important;
    color: white;
    width: 100% !important;
    padding:2vh 2vw 2vw 2vw;
}
.contactOne{
    width: 100%;
}

.aboutButton{
    font-family:"Montserrat-Regular.ttf"    !important;
    background-color: #12151E;
    border: 2px solid #FBBA14;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px;
    height: 7vh;
    color: #FBBA14;
    width: 24vw;
    cursor: pointer;
    transition: .4s;
    -webkit-transition: .4s;
    -moz-transition: .4s;
    -ms-transition: .4s;
    -o-transition: .4s;
    font-weight: 800;
    font-size: 18px;
}


.aboutButton:hover{
    border: 2px solid white;
    color: white;
}

