@font-face {
    font-family:"Akronim Regular.ttf";
    src: url(../DataImage/Akronim-Regular.ttf);
}
 
@font-face {
    font-family:"Oswald-VariableFont wght.ttf";
    src: url(../DataImage/Oswald-VariableFont_wght.ttf);
}
@font-face {
    font-family:"Montserrat-Regular.ttf";
    src: url(../DataImage/Montserrat-Regular.ttf);
}

* {
    font-family:"Oswald-VariableFont wght.ttf";
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.around {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.between {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.only-column {
    display: flex;
    flex-direction: column;
}

.only-row {
    display: flex;
    flex-direction: row;
}
 
#ok{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: black;
    opacity: 0%;
    z-index: 0;
    display: none;
}
.menu{
    width: 100%;
    height: 100px;
    align-items: flex-start;
    justify-content: space-evenly;
    padding: 30px 0 0 0;
}
.header{
    background-color: #0B0D13;
    width: 100%;
     padding: 0 0 50px 0;

}
.liOne svg{
    width: 3vw;
    height: 4vh;
    fill: #B3B2B1;
    transition: .4s;
    -webkit-transition: .4s;
    -moz-transition: .4s;
    -ms-transition: .4s;
    -o-transition: .4s;
}
.name{
   color: #B3B2B1;
   transition: .4s;
   margin-right: 20px;
   cursor: pointer;
   -webkit-transition: .4s;
   -moz-transition: .4s;
   -ms-transition: .4s;
   -o-transition: .4s;
   font-weight: 300;
   position: relative;
}
.name:hover{
   color: #FBBA14;
}
.name:hover .pricee{
    color: #0B0D13 !important;
    background-color: #FBBA14;
    border: 1px solid #0B0D13;
}
.pricee{
    transition: 0.4s;
    position: absolute;
    top: -1.8vh;
    left: 1.6vh;
    font-weight: 800;
    color: #0B0D13 !important;
    background-color: #B3B2B1;
    border: 1px solid #0B0D13;
    width: 1.5vw;
    height: 1.5vw;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    font-size: 11px;
    font-family:"Montserrat-Regular.ttf";
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
}

